.colors-sheet {
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-align-content: center;
  -ms-flex-line-pack: center;
  align-content: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
}

.color-block {
  line-height: 20px;
  padding: 10px;
  order: 0;
  flex: 0 1 auto;
  align-self: auto;
  text-align: center;
  cursor: pointer;
}

.demo {
  padding: 0!important;
  margin-bottom: 1em;
  border-radius: var(--border-radius)!important;
  font-size: 14px;
}

@media (min-width: 1200px) {
  .color-block {
    width: calc(100% / 16);
  }
  .demo > div:nth-child(1) {
    border-top-left-radius: var(--border-radius);
  }

  .demo > div:nth-child(16) {
    border-top-right-radius: var(--border-radius);
  }

  .demo > div:nth-last-child(1) {
    border-bottom-right-radius: var(--border-radius);
  }
  .demo > div:nth-last-child(16) {
    border-bottom-left-radius: var(--border-radius);
  }
}

@media (max-width: 1200px) and (min-width: 800px) {
  .color-block {
    width: calc(100% / 8);
  }
  .demo > div:nth-child(1) {
    border-top-left-radius: var(--border-radius);
  }

  .demo > div:nth-child(8) {
    border-top-right-radius: var(--border-radius);
  }

  .demo > div:nth-last-child(1) {
    border-bottom-right-radius: var(--border-radius);
  }
  .demo > div:nth-last-child(8) {
    border-bottom-left-radius: var(--border-radius);
  }
}

@media (max-width: 800px) and (min-width: 400px) {
  .color-block {
    width: calc(100% / 4);
  }
  .demo > div:nth-child(1) {
    border-top-left-radius: var(--border-radius);
  }

  .demo > div:nth-child(4) {
    border-top-right-radius: var(--border-radius);
  }

  .demo > div:nth-last-child(1) {
    border-bottom-right-radius: var(--border-radius);
  }
  .demo > div:nth-last-child(4) {
    border-bottom-left-radius: var(--border-radius);
  }
}


@media (max-width: 400px) {
  .color-block {
    width: calc(100% / 2);
  }
  .demo > div:nth-child(1) {
    border-top-left-radius: var(--border-radius);
  }

  .demo > div:nth-child(2) {
    border-top-right-radius: var(--border-radius);
  }

  .demo > div:nth-last-child(1) {
    border-bottom-right-radius: var(--border-radius);
  }
  .demo > div:nth-last-child(2) {
    border-bottom-left-radius: var(--border-radius);
  }
}

.slider {
  width: 100%;
  -webkit-appearance: none;
  height: 10px;
  border-radius: 5px;
  margin: 1em 0;
}
.red-slider {
  background: linear-gradient(90deg,
    #000, #f00
  );
}
.green-slider {
  background: linear-gradient(90deg,
    #000, #0f0
  );
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 20px;
  height: 20px;
  border-radius: 10px;
  background: #fff;
}
.slider::-moz-range-thumb {
  width: 20px;
  height: 20px;
  border-radius: 10px;
  background: #fff;
}

.pagination {
  margin: 0;
}
